import {
	submitOrderData,
	wechatPayOrderData
} from "../../api/order/index.js"

export default {
	namespaced: true,
	state: {

	},
	mutations: {

	},
	actions: {
		submitOrder(context, payload) {
			let data = {
				uid: context.rootState.login.uid, //id
				token: context.rootState.login.token, //token
				platform: context.rootState.system.plateform, // 1
				branch_shop_id: payload.orderData.branch_shop_id, //id
				table_code: payload.orderData.table_code, //
				remarks: payload.orderData.remarks, //
				is_pack: payload.orderData.is_pack, // 10
				distribution_type: payload.orderData.distribution_type // 20
			}
			submitOrderData(data).then(res => {
				if (payload.completed) {
					payload.completed(res)
				}
			})
		},
		 wechatPayOrder(context, payload) {
			let data = {
				uid: context.rootState.login.uid, //id
				token: context.rootState.login.token, //token
				platform: context.rootState.system.plateform, // 1
				// open_id: context.rootState.login.openId, //ID  
				open_id:"oZU0G5l_PRbTHp7gPlkXW-kDFDVo",
				body: context.rootState.business.shopInfo.branch_shop_name + "",
				...payload
			}
			return wechatPayOrderData(data).then(res => {
				 return res
			})
		}
	}
}
